import Search from '@/components/search';
import { getDeviceType, deleteDeviceType } from '@/api/devicelist';
import Detail from "./detail";
import { pagging, buttonPermissions } from '@/mixin';
import BindView from "../list/bindView";
export default {
  name: 'Type',
  components: {
    Search: Search,
    Detail: Detail,
    BindView: BindView
  },
  mixins: [pagging, buttonPermissions],
  data: function data() {
    return {
