      var _this2 = this;

      this.loading = true;
      getDeviceType({
        keyword: this.keyword,
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      }).then(function (res) {
        _this2.loading = false;

        _this2.$set(_this2, 'tableData', res.data.items);

