import { updateDeviceType, createDeviceType } from '@/api/devicelist';
export default {
  /* eslint-disable */
  data: function data() {
    return {
      visible: false,
      type: 'insert',
      rules: {
        name: [{
          required: true,
          message: this.$t('type.rules.enterName'),
          trigger: 'blur'
        }],
        code: [{
          required: true,
          message: this.$t('type.rules.enterCode'),
          trigger: 'blur'
        }],
        remark: [{
          required: true,
          message: this.$t('type.rules.enterRemark'),
          trigger: 'blur'
        }]
      },
      form: {}
    };
  },
  methods: {
    init: function init(obj) {
      this.type = obj == null ? 'insert' : 'edit';
      this.visible = true;

      if (obj) {
        this.form = obj;
      }
    },
    onClose: function onClose() {
      this.$parent.detailShow = false;
      this.form = '';
    },
    cancel: function cancel() {
      this.visible = false;
    },
    onSubmit: function onSubmit(formName) {
      var _this = this;

      this.$refs[formName].validate(function (valid) {
        if (valid) {
          if (_this.type == 'edit') {
            updateDeviceType(_this.form).then(function (res) {
              _this.$message({
                type: 'success',
                message: _this.$t('rules.successfulRevision'),
                duration: 500,
                onClose: function onClose() {
                  _this.onClose();
                }
              });
            });
          } else if (_this.type == 'insert') {
            createDeviceType(_this.form).then(function (res) {
              _this.$message({
                type: 'success',
                message: _this.$t('rules.newsuccess'),
                duration: 500,
                onClose: function onClose() {
                  _this.onClose();

                  _this.$parent.init();
                }
              });
            });
          }
        }
      });
    }
  }
};